<template>
	<div class="news">
		<h2>
			<span>已发表内容</span>
			<icon type="search" size="18" style="height:30px;"></icon>
		</h2>
		<list class="list" v-if="newList.length">
			<!-- 注意事项: 不能使用 index 作为 key 的唯一标识 -->
			<cell v-for="item in newList" :key="item.media_id" class="cell">
				<cell v-for="items in item.content.news_item" :key="items.thumb_url">
					<div class="left">
						<span>{{item.update_time}}</span>
						<span>{{items.title}}</span>
					</div>
					<div class="right">
						<div :style="`background:url(${items.thumb_url}) 50% center / cover no-repeat;`"></div>
					</div>
				</cell>
			</cell>
		</list>
	</div>
</template>

<script>
	import {request} from 'utils';
	export default{
		data(){
			return{
				access_token:'',
				count:1,
				newList:[]
			}
		},
		onPullDownRefresh () {
			this.count += 1;
			this.getAccessToken();
		},
		onShow(){
			this.getAccessToken();
			wx.showShareMenu({
				withShareTicket:true,
				menus:['shareAppMessage','shareTimeline']
			})
		},
		methods:{
			getAccessToken(){
				request(`https://api.weixin.qq.com/cgi-bin/token?grant_type=client_credential&appid=wx9648e7189f21d18c&secret=bf47c54af70f41c4b39ae696b504ea2e`)
				.then(({data})=>{
					this.access_token = data.access_token;
					this.getNewsList();
				})
				.catch(err=>{
					console.log(err)
				})
			},
			getNewsList(){
				const data = {
					type:'news',
					offset:(this.count - 1) * 10,
					count:10
				}
				const header = {
					content_type:'application/json'
				}
				request(`https://api.weixin.qq.com/cgi-bin/material/batchget_material?access_token=${this.access_token}`,header,data,'post')
				.then(res=>{
					this.newList = res.data.item.map(o=>Object.assign({},o,{update_time:new Date(o.update_time * 1000).toLocaleString()}));
				})
				.catch(err=>{
					console.log(err)
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import 'index.scss';
</style>
